import React, { useEffect, useState } from "react";
import "./index.css";
import { realtimeDB } from "./config";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import profile from "./img/profile.png";
import logout from "./img/logout.jpg";
import donar from "./img/donar.jpg";
import approvedImg from "./img/approved.png";
import rejectedImg from "./img/rejected.png";
import outOfStockImg from "./img/outofstock.png";
//ALL IMPORT END HERE---------------------------

function Login(props) {
  const [loader, setLoader] = useState(null);
  const [data, setData] = useState({});
  const [isDiabled, disableSaveButton] = useState(false);
  const [success, setSuccessMsg] = useState({ type: 0, msg: "" });
  const [authenticated, setAuthenticated] = useState(
    sessionStorage.getItem("foodForAllLogin") || false
  );
  const [dashboardType, setDashboard] = useState(null);
  const [userInfo, setUserInfo] = useState({});
  const [response, setResponse] = useState({});
  const [updateStatus, setUpdateStatus] = useState(false);
  const [val, setVal] = useState("");
  const [open, setOpen] = useState(false);
  const [foodID, setFoodID] = useState(null);
  const [curQuantity, setCurQuantity] = useState(0);

  // SIGN-IN FROM FIREBASE
  const signin = (e) => {
    e.preventDefault();
    setLoader(true);
    if (!data) {
      return;
    }
    const usersRef = realtimeDB.ref(`admin/`);
    usersRef
      .get()
      .then((snapshot) => {
        if (snapshot.exists()) {
          if (
            data.uid === snapshot.val().uid &&
            data.pwd === snapshot.val().pwd
          ) {
            setUserInfo(snapshot.val());
            sessionStorage.setItem("foodForAllLogin", true);
            setAuthenticated(true);
          } else {
            sessionStorage.removeItem("foodForAllLogin");
            setSuccessMsg({ type: 0, msg: "Invalid User Name or Password" });
          }
        } else {
          setSuccessMsg({ type: 0, msg: "No Data Available" });
        }
        setLoader(false);
      })
      .catch(() => {
        setLoader(false);
        setSuccessMsg({
          type: 0,
          msg: "Some Error occoured, Please Try again",
        });
      });
  };

  // FETCHING ALL DATA FROM FIREBASE AND STROING IT INTO LOCAL STATE
  useEffect(() => {
    const usersRef = realtimeDB.ref(`food/`);
    usersRef
      .get()
      .then((snapshot) => {
        if (snapshot.exists()) {
          setResponse(snapshot.val());
        } else {
          setSuccessMsg({ type: 0, msg: "No Data Available" });
        }
      })
      .catch((error) => {
        console.error(error);
        setSuccessMsg({
          type: 0,
          msg: "Some Error occoured, Please Try again",
        });
      });
  }, [authenticated, updateStatus, open]);

  // FOR FIRST TIME ADMIN CRED CREATION IN FIREBASE
  const setNewLogin = () => {
    const usersRef = realtimeDB.ref(`admin/`);
    usersRef
      .set({
        uid: "admin",
        pwd: "password",
      })
      .then(() => {
        console.log(" Data saved successfully!");
      })
      .catch((error) => {
        console.log("Some Error occoured!! Please try again...");
      });
  };

  // FUNCTION FOR ADMIN LOGOUT
  const logoutAdmin = () => {
    setAuthenticated(false);
    sessionStorage.removeItem("foodForAllLogin");
  };

  // ADMIN USERNAME PASSWORD UPDATE FUNCTION
  const updateAdminCred = () => {
    const usersRef = realtimeDB.ref(`admin/`);
    usersRef
      .set(data)
      .then(() => {
        setSuccessMsg({ type: 1, msg: "Updated Successfully" });
      })
      .catch(() => {
        setSuccessMsg({
          type: 0,
          msg: "Some Error occoured, Please Try again",
        });
      });
  };

  // SET DATA TO LOCAL STATE TO MAKE IT READY FOR SAVE IN FIREBASE
  const handeData = (e) => {
    setSuccessMsg({ type: 0, msg: "" });
    let temp = data;
    data[e.target.name] = e.target.value;
    setData(temp);
  };

  // DASHBOARD ITEMS SWITCH
  const dashboardItems = () => {
    switch (dashboardType) {
      case "profile":
        return profileUpdate();

      case "donarRequest":
        return donarRequest();

      default:
        return initialMenu();
    }
  };

  // DASHBOARD ITEM INTIAL MENU WHICH WILL APPEAR AFTER SIGN-IN
  const initialMenu = () => {
    return (
      <div className="dashboard">
        <div className="row">
          <div className="col-lg-3 col-md-4 col-sm-12 m-2">
            <Card sx={{ minWidth: 275, textAlign: "center" }}>
              <CardContent>
                <img src={profile} className="card-img" />
                <h4>My Profile</h4>
              </CardContent>
              <CardActions>
                <Button
                  size="large"
                  onClick={() => setDashboard("profile")}
                  fullWidth
                  variant="contained"
                >
                  VIEW MY Profile
                </Button>
              </CardActions>
            </Card>
          </div>
          <div className="col-lg-3 col-md-4 col-sm-12 m-2">
            <Card sx={{ minWidth: 275, textAlign: "center" }}>
              <CardContent>
                <img src={donar} className="card-img" />
                <h4>Request</h4>
              </CardContent>
              <CardActions>
                <Button
                  size="large"
                  onClick={() => setDashboard("donarRequest")}
                  fullWidth
                  variant="contained"
                >
                  VIEW donar request
                </Button>
              </CardActions>
            </Card>
          </div>
          <div className="col-lg-3 col-md-4 col-sm-12 m-2">
            <Card sx={{ minWidth: 275, textAlign: "center" }}>
              <CardContent>
                <img src={logout} className="card-img" />
                <h4>Logout</h4>
              </CardContent>
              <CardActions>
                <Button
                  size="large"
                  fullWidth
                  variant="contained"
                  onClick={() => logoutAdmin()}
                >
                  click Here
                </Button>
              </CardActions>
            </Card>
          </div>
        </div>
      </div>
    );
  };

  // MY PROFILE UPDATE RAW HTML WHICH WILL APPEAR ON MY PROFILE SECTION
  const profileUpdate = () => {
    return (
      <div className="dashboard">
        <Button
          size="large"
          onClick={() => setDashboard(null)}
          className="backbtn"
          variant="contained"
        >
          BACK
        </Button>

        <form>
          <Card
            sx={{ width: "40%", margin: "5% auto 0 auto", opacity: "0.99" }}
          >
            <h3 className="m-3 text-center">MY PROFILE</h3>
            <CardContent>
              <TextField
                label="User Name"
                name="uid"
                defaultValue={userInfo.uid}
                variant="outlined"
                margin="dense"
                fullWidth
                onChange={(e) => handeData(e)}
              />
              <TextField
                label="Password"
                name="pwd"
                defaultValue={userInfo.pwd}
                type="text"
                variant="outlined"
                margin="dense"
                fullWidth
                onChange={(e) => handeData(e)}
              />
            </CardContent>
            <div className="m-3">
              <h6 className={success.type ? "text-success" : "text-danger"}>
                {success.msg}
              </h6>
            </div>

            <CardActions>
              <Button
                size="large"
                onClick={() => setDashboard(null)}
                fullWidth
                variant="contained"
              >
                Back
              </Button>
              <Button
                size="large"
                onClick={() => updateAdminCred()}
                fullWidth
                variant="contained"
              >
                UPDATE
              </Button>
            </CardActions>
          </Card>
        </form>
      </div>
    );
  };

  // OPEN POPUP OF "NEW FOOD REQUEST"
  const openRequest = (id, data, currentQuantity) => {
    setFoodID(id);
    setOpen(data);
    setCurQuantity(currentQuantity);
  };

  // ALL DONAR'S ROW OF "VIEW DONAR REQUEST"
  const getRow = () => {
    let row = [];
    for (const key in response) {
      row.push(
        <tr
          key={key}
          className={
            response[key].dR.status === "live"
              ? "table-success"
              : response[key].dR.status === "cancel"
              ? "table-danger"
              : response[key].dR.status === "completed"
              ? "table-secondary"
              : "table-warning"
          }
        >
          <td data-label="Image">
            {response[key].dR.url ? (
              <img src={response[key].dR.url} className="card-img2" />
            ) : (
              "NA"
            )}
          </td>
          <td data-label="Donation Date">
            {response[key].dR.reqDate || "NA"}
            <br />
            Status:{" "}
            <span
              class={
                response[key].dR.status == "live"
                  ? "p-1 bg-success rounded badge"
                  : response[key].dR.status === "cancel"
                  ? "p-1 bg-danger rounded badge"
                  : "text-dark p-1 bg-warning rounded badge"
              }
            >
              {response[key].dR.status || "NA"}
            </span>
            <br />
            Type:
            <span
              class={
                response[key].dR.foodType == "Non-vegetarian"
                  ? "p-1 bg-danger rounded badge"
                  : "p-1 bg-success rounded badge"
              }
            >
              {response[key].dR.foodType || "NA"}
            </span>
          </td>
          <td data-label="Name">
            {response[key].dR.name || "NA"}
            <br />
            {response[key].dR.mobile || "NA"}
          </td>
          <td
            data-label="Address"
            className="col-lg-2 col-md-3 col-sm-12"
            title={response[key].dR.address}
          >
            {response[key].dR.address || "NA"}
          </td>
          <td
            data-label="Description"
            className="col-lg-2 col-md-3 col-sm-12"
            title={response[key].dR.description}
          >
            {response[key].dR.foodName || "NA"}
            <br />
            {response[key].dR.description || "NA"}
            <br />
            Quantity: {response[key].dR.quantity || "NA"}
            <br />
            Hygiene Level:{response[key].dR.hygieneLevel || "NA"}%
          </td>
          <td data-label="Best Time">
            Cooked: {response[key].dR.cookedTime || "NA"}
            <br />
            Expiry: {response[key].dR.expiryTime || "NA"}
          </td>
          <td data-label="Action">
            {response[key].fR && (
              <button
                className="shadow w-100 py-1 bg-warning text-dark rounded"
                onClick={() =>
                  openRequest(key, response[key].fR, response[key].dR.quantity)
                }
              >
                {Object.keys(response[key].fR).length} Food Request
              </button>
            )}
            <br />
            <button
              className="shadow w-100 py-1 bg-success text-white rounded mt-2"
              size="small"
              color="warning"
              fullWidth
              variant="contained"
              onClick={() => handleUpdate(key, response[key].dR.status)}
            >
              Change Status
            </button>
          </td>
        </tr>
      );
    }
    return row;
  };

  // SAVE STATUS OF FOOD REQUEST IN FIREBASE
  const saveApproveReq = (key, action, askQuantity) => {
    disableSaveButton(true);
    if (action === "approved") {
      let newQuantity = curQuantity - (askQuantity || 0);

      let val = { quantity: newQuantity };
      if (newQuantity < 0) {
        val = {
          quantity: 0,
          status: "completed",
        };
      }

      const usersRefs = realtimeDB.ref(`food/${foodID}/dR/`);
      usersRefs
        .update(val)
        .then(() => {
          setUpdateStatus(null);
          setOpen(null);
          disableSaveButton(false);
        })
        .catch((error) => {
          disableSaveButton(false);
          console.error(error);
        });
    }

    const usersRef = realtimeDB.ref(`food/${foodID}/fR/${key}`);
    usersRef
      .update({ status: action })
      .then(() => {
        setUpdateStatus(null);
        disableSaveButton(false);
        setOpen(null);
      })
      .catch((error) => {
        disableSaveButton(false);
      });
  };

  // RAW HTML OF "NEW FOOD REQUEST" IN POPUP
  const getRequestRow = () => {
    let row = [];
    for (const key in open) {
      row.push(
        <tr key={key}>
          <td data-label="Name">
            {open[key].name || "NA"}
            <br />
            {open[key].mobile || "NA"}
            <br />
            Requested Quantity: {open[key].rQ || "NA"}
          </td>
          <td
            data-label="Reason"
            className="col-lg-2 col-md-3 col-sm-12"
            title={open[key].reason}
          >
            {open[key].reason || "NA"}
          </td>
          <td data-label="Action">
            {open[key].status === "pending" && curQuantity === 0 ? (
              <img src={outOfStockImg} className="foodRequestStatusImage" />
            ) : (
              <>
                {open[key].status === "pending" && (
                  <>
                    {console.log(isDiabled)}
                    <button
                      className="shadow w-100 py-1 bg-success text-white rounded"
                      disabled={isDiabled}
                      onClick={() =>
                        saveApproveReq(key, "approved", open[key].rQ)
                      }
                    >
                      Approve
                    </button>
                    <button
                      className="shadow w-100 py-1 bg-danger text-white rounded mt-2"
                      disabled={isDiabled}
                      size="small"
                      color="warning"
                      fullWidth
                      variant="contained"
                      onClick={() => saveApproveReq(key, "rejected")}
                    >
                      Reject
                    </button>
                  </>
                )}
              </>
            )}
            {open[key].status == "approved" && (
              <img src={approvedImg} className="foodRequestStatusImage" />
            )}
            {open[key].status === "rejected" && (
              <img src={rejectedImg} className="foodRequestStatusImage" />
            )}
          </td>
        </tr>
      );
    }
    return row;
  };

  //   CHANGE OPTION IN DROPDOWN OF DONAR REQUEST "CHANGE STATUS BUTTON"
  const handleUpdate = (key, currentStatus) => {
    setUpdateStatus(key);
    setVal(currentStatus);
  };

  //   SAVE STATUS OF DONAR REQUEST IN FIREBASE
  const saveStatus = () => {
    setLoader(true);
    const usersRef = realtimeDB.ref(`food/${updateStatus}/dR`);
    switch (val) {
      case "pending":
      case "cancel":
      case "live":
      case "completed":
        usersRef
          .update({ status: val })
          .then(() => {
            setUpdateStatus(null);
            setLoader(false);
          })
          .catch(() => {
            setLoader(false);
            setSuccessMsg({
              type: 0,
              msg: "Some Error occoured, Please Try again",
            });
          });
        break;
      case "delete":
        realtimeDB
          .ref(`food/${updateStatus}/`)
          .remove()
          .then(() => {
            setLoader(false);
            setUpdateStatus(null);
          })
          .catch((error) => {
            setLoader(false);
            setSuccessMsg({
              type: 0,
              msg: "Some Error occoured, Please Try again",
            });
          });
        break;
    }
  };

  const donarRequest = () => {
    return (
      <div className="row m-0 p-0 dashboard">
        <div className="col-12 m-0 p-0 table-responsive-md">
          <Button
            size="large"
            onClick={() => setDashboard(null)}
            className="backbtn"
            variant="contained"
          >
            BACK
          </Button>
          <table className="table table-hover col-12 m-0 p-0">
            <thead>
              <tr className="table-dark text-white sticky">
                <th scope="col">Picture</th>
                <th scope="col">Donation Date</th>
                <th scope="col">DONAR Name</th>
                <th scope="col">Food Pickup Address</th>
                <th scope="col">Description</th>
                <th scope="col">Time</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>{getRow()}</tbody>
          </table>
        </div>
      </div>
    );
  };

  // ALL HTML WHICh IS SHOWING ON DASHBOARD PAGE
  return (
    <React.Fragment>
      {authenticated ? (
        <React.Fragment>{dashboardItems()}</React.Fragment>
      ) : (
        <form className="login">
          <Card
            sx={{ width: "40%", margin: "5% auto 0 auto", opacity: "0.99" }}
          >
            <h3 className="m-3 text-center">Admin LOGIN</h3>
            <CardContent>
              <TextField
                label="User Name"
                name="uid"
                variant="outlined"
                margin="dense"
                fullWidth
                onChange={(e) => handeData(e)}
              />
              <TextField
                label="Password"
                name="pwd"
                type="password"
                variant="outlined"
                margin="dense"
                fullWidth
                onChange={(e) => handeData(e)}
              />
            </CardContent>
            <div className="m-3">
              <h6 className="text-danger">{success.msg}</h6>
            </div>
            <CardActions>
              <Button
                type="submit"
                variant="contained"
                onClick={signin}
                disabled={loader}
                fullWidth
                sx={{ height: "60px" }}
              >
                {" "}
                SIGN-IN
              </Button>
            </CardActions>
          </Card>
        </form>
      )}
      <Dialog
        maxWidth="sm"
        fullWidth={true}
        onClose={() => setUpdateStatus(false)}
        open={updateStatus}
      >
        <DialogTitle>Change Action</DialogTitle>
        <div className="m-4">
          <FormControl fullWidth margin="dense">
            <InputLabel id="demo-simple-select-helper-label">
              Update Status
            </InputLabel>
            <Select
              value={val}
              fullWidth
              name="foodType"
              onChange={(e) => setVal(e.target.value)}
            >
              <MenuItem value="pending">
                Waiting for Approval (Pending)
              </MenuItem>
              <MenuItem value="live">Approve (Live) </MenuItem>
              <MenuItem value="cancel">Reject (Cancel) </MenuItem>
              <MenuItem value="completed">Completed </MenuItem>
              <MenuItem value="delete">Delete ROW</MenuItem>
            </Select>
          </FormControl>
          <div className="text-right">
            <Button variant="contained" onClick={() => setUpdateStatus(false)}>
              Cancel
            </Button>
            <Button
              type="submit"
              variant="contained"
              className="m-2"
              disabled={loader}
              onClick={saveStatus}
            >
              Update
            </Button>
          </div>
        </div>
      </Dialog>

      <Dialog
        maxWidth="lg"
        fullWidth={true}
        onClose={() => setOpen(false)}
        open={open}
      >
        <div className="row m-0">
          <DialogTitle className="col-11">Food Request</DialogTitle>
          <div
            className="text-right col-1 p-4"
            role="button"
            onClick={() => setOpen(false)}
          >
            
          </div>
        </div>
        <div className="m-4">
          <table className="table table-hover col-12 m-0 p-0">
            <thead>
              <tr className="table-dark text-white sticky">
                <th scope="col" className="col-4">
                  Name
                </th>
                <th scope="col" className="col-6">
                  Reason (Additional Info)
                </th>
                <th scope="col" className="col-2">
                  Status
                </th>
              </tr>
            </thead>
            <tbody>{getRequestRow()}</tbody>
          </table>
          <div className="text-right mt-4">
            <Button
              variant="outlined"
              className="mx-2"
              onClick={() => setOpen(false)}
            >
              Cancel
            </Button>
          </div>
        </div>
      </Dialog>
    </React.Fragment>
  );
}

export default Login;
