import React, { useEffect, useState } from "react";
import { HashRouter as Router, Routes, Route, Link } from "react-router-dom";
import "./App.css";
import logo from "./img/logo.png";
import Home from "./Home";

import About from "./About";
import Visitor from "./Visitor";
import Donar from "./Donar";
import Login from "./Login";

function App(props) {
  const [affix, setAffix] = useState(true);
  const [showList, setShowList] = useState(false);

  useEffect(() => {
    //  Function used to shrink nav bar removing paddings and adding black background
  }, []);

  const openMenu = () => {
    setShowList(!showList);
  };

  return (
    <Router>
      <nav className={affix ? "nav affix" : "nav"}>
        <div className="containerTop">
          <div className="logo">
            <Link to="/">
              <img className="logo" src={logo} />
            </Link>
          </div>
          <div
            id="mainListDiv"
            className={showList ? "show_list main_list" : "main_list"}
            style={
              showList
                ? { display: "block", backdropFilter: "blur(10px)" }
                : null
            }
          >
            <ul className="navlinks">
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/about">About us</Link>
              </li>
              <li>
                <Link to="/visitor">Request Food</Link>
              </li>
              <li>
                <Link to="/donate">Donate Now</Link>
              </li>
              <li>
                <Link to="/admin">Admin</Link>
              </li>
            </ul>
          </div>
          <span
            className={showList ? "navTrigger active" : "navTrigger"}
            onClick={openMenu}
          >
            <i></i>
            <i></i>
            <i></i>
          </span>
        </div>
      </nav>

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/about" element={<About />} />
        <Route path="/visitor" element={<Visitor />} />
        <Route path="/donate" element={<Donar />} />
        <Route path="/admin" element={<Login />} />
      </Routes>
    </Router>
  );
}

export default App;
