import React, { useEffect, useState } from "react";
import { realtimeDB } from "./config";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
// import noAvailable from "./img/menu-unavailable.png";
import noAvailable from "./img/nofood.png";

function Visitor(props) {
  const [response, setResponse] = useState({});
  const [success, setSuccessMsg] = useState(null);
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({});
  const [isDisabled, setIsDisabled] = useState(true);

  useEffect(() => {
    const usersRef = realtimeDB.ref(`food/`);
    usersRef
      .get()
      .then((snapshot) => {
        if (snapshot.exists()) {
          setResponse(snapshot.val());
        } else {
          setSuccessMsg("No Data Available");
        }
      })
      .catch((error) => {
        console.error(error);
        setSuccessMsg("Some Error occoured, Please Try again");
      });
  }, []);

  const reqNow = (key) => {
    setOpen(key);
  };

  const getRow = () => {
    let rows = [];
    let anyLive = false;
    for (const key in response) {
      if (response[key].dR.status === "live") {
        anyLive = true;
        rows.push(
          <div className="col-lg-3 col-md-4 col-sm-12 m-2">
            <Card className="bg-light shadow-md">
              <CardContent>
                <div key={key}>
                  <div data-label="Image">
                    {response[key].dR.url ? (
                      <img src={response[key].dR.url} className="card-img3" />
                    ) : (
                      "NA"
                    )}
                  </div>
                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">Food Name</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.foodName || "NA"}
                    </div>
                  </div>
                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">Description</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.description || "NA"}
                    </div>
                  </div>
                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">Quantity</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.quantity || "NA"}
                    </div>
                  </div>
                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">Cooked Time</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.cookedTime || "NA"}
                    </div>
                  </div>

                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">Expiry Time</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.expiryTime || "NA"}
                    </div>
                  </div>

                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">
                      Hygiene Level
                    </div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.hygieneLevel || "NA"}
                    </div>
                  </div>

                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">Food Type</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.foodType || "NA"}
                    </div>
                  </div>
                  <div className="row">
                    <div className="text-left col-6 px-4 my-1">DATE</div>
                    <div className="text-left col-6 px-4 my-1">
                      {response[key].dR.reqDate || "NA"}
                    </div>
                  </div>
                </div>
              </CardContent>
              <CardActions>
                <Button
                  size="large"
                  onClick={() => reqNow(key)}
                  fullWidth
                  variant="contained"
                >
                  Request Now
                </Button>
              </CardActions>
            </Card>
          </div>
        );
      }
    }
    if (!anyLive) {
      rows.push(
        <div className="row bg-img m-0 p-0">
          <Card
            sx={{ minWidth: "450px", height: "600px", opacity: "0.95" }}
            className="text-center w-100 m-auto my-4"
          >
            <img
              src={noAvailable}
              className="card-img mb-2"
              style={{ height: "400px", width: "auto" }}
            />
            <h1 className="text-center w-100 bg-secondary m-0 p-0">
              <span className="text-white">Please Check back soon.</span>
            </h1>
          </Card>
        </div>
      );
    }
    return rows;
  };

  // SET DATA TO LOCAL STATE TO MAKE READY FOR SAVE
  const handeData = (e) => {
    let temp = data;
    data[e.target.name] = e.target.value;
    setData(temp);
    Object.keys(temp).length && setIsDisabled(false);
  };

  const saveRequest = () => {
    setIsDisabled(true);
    if (data.name && data.mobile && data.rQ) {
      const usersRef = realtimeDB.ref(`food/${open}/fR`);
      usersRef
        .push({ ...data, status: "pending" })
        .then(() => {
          setOpen(null);
          setIsDisabled(false);
          alert(
            "Food request raised, waiting for approval once approved you will be informed on the given contact number"
          );
        })
        .catch((error) => {
          setSuccessMsg("Some Error occoured, Please Try again");
        });
    } else {
      alert("Please Fill the all Required Field");
    }
  };

  return (
    <div className="visitor">
      <div className="row">{getRow()}</div>
      <Dialog
        maxWidth="sm"
        fullWidth={true}
        onClose={() => setOpen(false)}
        open={open}
      >
        <div className="row m-0">
          <DialogTitle className="col-10">Food Request</DialogTitle>
          <div
            className="text-right col-1 m-4"
            role="button"
            onClick={() => setOpen(false)}
          >
            
          </div>
        </div>
        <p className="mx-4 my-0 p-0">Please Fill this form</p>
        <div className="m-4">
          <TextField
            label="Your Name"
            required
            name="name"
            variant="outlined"
            margin="dense"
            fullWidth
            onChange={(e) => handeData(e)}
          />
          <TextField
            label="Contact Number"
            required
            name="mobile"
            variant="outlined"
            margin="dense"
            fullWidth
            onChange={(e) => handeData(e)}
          />
          <TextField
            label="Quantity Required"
            required
            name="rQ"
            variant="outlined"
            type="number"
            margin="dense"
            fullWidth
            onChange={(e) => handeData(e)}
          />
          <TextField
            label="Reason (Additional Details)"
            name="reason"
            multiline
            rows={4}
            variant="outlined"
            margin="dense"
            fullWidth
            onChange={(e) => handeData(e)}
          />
          <div className="text-right mt-4">
            <Button
              variant="outlined"
              className="mx-2"
              onClick={() => setOpen(false)}
            >
              Cancel
            </Button>
            <Button
              type="submit"
              variant="contained"
              disabled={isDisabled}
              onClick={saveRequest}
            >
              Request Now
            </Button>
          </div>
        </div>
      </Dialog>
    </div>
  );
}

export default Visitor;
