import React, { useState } from "react"
import './index.css'
import { storage, realtimeDB } from "./config";
import Card from '@mui/material/Card';
import CircularProgress from '@mui/material/CircularProgress';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import correct from './img/correct.jpg'
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
//ALL IMPORT END HERE

function Donar(props) {
    const [image, setImg] = useState(null);
    const [loader, setLoader] = useState(null);
    const [foodType, setFoodType] = useState('Vegetarian');
    const [data, setData] = useState({})
    const [success, setSuccess] = useState({ type: 0, msg: null });
    const [open, setOpen] = React.useState(false);
    const [isDiabled, disableSaveButton] = useState(true);
    const [cookTime, setCookTime] = React.useState(new Date())
    const [expiryTime, setExpiryTime] = React.useState(new Date())

    const handleCookChange = (newValue) => {
        setCookTime(newValue);
    };
    const handleExpiryChange = (newValue) => {
        setExpiryTime(newValue);
    };
    // UPLOADING iMAGE TO FIRESOTRE OF FIREBASE
    const upload = () => {
        if (image == null) {
            disableSaveButton(true)
            return;
        }
        setLoader(true)
        storage.ref(`/images/${image.name}`).put(image)
            .on("state_changed",
                snapshot => { },
                error => {
                    console.log(error)
                },
                () => {
                    storage.ref('images')
                        .child(image.name)
                        .getDownloadURL()
                        .then(url => {
                            addDonarRequest(url)
                        })
                }
            );
    }


    // ADDING DATA TO FIREBASE AFTER IMAGE UPLOAD 
    const addDonarRequest = (url) => {
        let newDonationRequest = data;
        newDonationRequest.url = url;
        newDonationRequest.status = "pending";
        newDonationRequest.expiryTime = new Date(expiryTime).toDateString();
        newDonationRequest.cookedTime = new Date(cookTime).toDateString();
        newDonationRequest.foodType = foodType;
        newDonationRequest.reqDate = `${new Date().toDateString()}`;
        const usersRef = realtimeDB.ref(`food/`);


        usersRef.push({ dR: newDonationRequest }).then(() => {
            setLoader(false);
            setOpen(false)
            setData({});
            setSuccess({ type: 1, msg: "Request Raised, once approved from admin it will display under the 'Request Food' menu" })
        })
            .catch((error) => {
                setSuccess({ type: 0, msg: "Some Error occoured!! Please try again..." })
            });
    }


    // SET DATA TO LOCAL STATE TO MAKE READY FOR SAVE
    const handeData = (e) => {
        let temp = data;
        data[e.target.name] = e.target.value;
        setData(temp)
        setSuccess({ type: 0, msg: null })
    }

    // ACKNOWLEDGEMETN POPUP CLOSE METHOD
    const handleClose = () => {
        setOpen(false);
    };

    const isReadyForSave = (e) => {
        e.preventDefault();
        if (data.name && data.mobile && data.foodName && data.quantity) {
            setOpen(true);
        } else {
            setSuccess({ type: 0, msg: "Please fill all required Field." })
        }
    }

    // ALL HTML WHIC IS SHOWING ON DONAR PAGE 
    return (
        <div className="App donar">
            <Card sx={{ width: "90%", margin: "70px auto", opacity: "0.9" }}>
                <h3 className="m-3">Please Fill the Details</h3>
                <form>
                    <CardContent>
                        <div className="row">
                            <div className="col-6">
                                <TextField label="Name" name="name" value={data.name || ""} required variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} />
                            </div>
                            <div className="col-6">
                                <TextField label="Contact Number" value={data.mobile || ""} required name="mobile" variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} />
                            </div>
                        </div>

                        <TextField label="Food Pickup Address" value={data.address || ""} name="address" variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} />
                        <TextField label="Food Name" value={data.foodName || ""} name="foodName" required variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} />
                        <TextField label="Description" value={data.description || ""} name="description" variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} multiline rows={4} />
                        <div className="row">
                            <div className="col-6">
                                <TextField label="Quantity" value={data.quantity || ""} required name="quantity" type="number" variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} />
                            </div>
                            <div className="col-6">
                                <TextField label="Hygiene Level (in %)" value={data.hygieneLevel || ""} name="hygieneLevel" type="number" variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-6">
                                <LocalizationProvider dateAdapter={AdapterDateFns}>
                                    <DesktopDatePicker
                                        inputFormat="MM/dd/yyyy"
                                        label="Cooked Time"
                                        name="cookedTime"
                                        onChange={handleCookChange}
                                        value={cookTime}
                                        renderInput={(params) => <TextField {...params} margin="dense" fullWidth />}
                                    />
                                </LocalizationProvider>
                            </div>
                            <div className="col-6">
                                <LocalizationProvider dateAdapter={AdapterDateFns}>
                                    <DesktopDatePicker
                                        inputFormat="MM/dd/yyyy"
                                        label="Expiry Time"
                                        name="expiryTime"
                                        onChange={handleExpiryChange}
                                        value={expiryTime}
                                        renderInput={(params) => <TextField {...params} margin="dense" fullWidth />}
                                    />
                                </LocalizationProvider>
                                {/* <TextField label="Expiry Time" name="expiryTime" variant="outlined" margin="dense" fullWidth onChange={(e) => handeData(e)} /> */}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-6">
                                <FormControl fullWidth margin="dense">
                                    <InputLabel id="demo-simple-select-helper-label">Food Type</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-helper-label"
                                        value={foodType}
                                        fullWidth
                                        name="foodType"
                                        onChange={(e) => setFoodType(e.target.value)}
                                    >
                                        <MenuItem value='Vegetarian'>Vegetarian</MenuItem>
                                        <MenuItem value='Non-vegetarian'>Non-vegetarian</MenuItem>
                                        <MenuItem value='Fruits'>Fruits</MenuItem>
                                        <MenuItem value='Dairy-Food'>Dairy foods</MenuItem>
                                        <MenuItem value='Veg+NonVeg'>Veg+NonVeg</MenuItem>
                                    </Select>
                                </FormControl>
                            </div>
                            <div className="col-6 mt-4">
                                <label for="image" className="pic">Picture<sup className="text-danger">*</sup>:</label>
                                <input id="image" accept="image/*" type="file" onChange={(e) => { setImg(e.target.files[0]); disableSaveButton(false) }} />
                            </div>
                        </div>

                    </CardContent>
                    <div className="m-3">
                        <h4 className={success.type ? "text-success" : "text-danger"}>{success.msg}</h4>
                    </div>
                    <CardActions>
                        <Button type="submit" variant="contained" disabled={isDiabled} onClick={(e) => isReadyForSave(e)} fullWidth sx={{ height: "60px" }}> SEND</Button>
                    </CardActions>
                </form>

            </Card>

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    Acknowledgment
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">

                        To the best of my knowledge these food is eatable.
                        <img src={correct} className=" card-img" />
                        <div className="row mt-2">
                            <div className="col-12">Name: {data.name}</div>
                        </div>
                        <div className="row">
                            <div className="col-12">Date: {`${new Date().getDate()}/${new Date().getMonth()}/${new Date().getYear()}`}</div>
                        </div>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button variant="outlined" onClick={handleClose}>Disagree</Button>
                    <Button variant="contained" onClick={upload} autoFocus disabled={loader}>
                        {loader ? <CircularProgress color="inherit" size={25} /> : "Agree"}
                    </Button>
                </DialogActions>
            </Dialog>
        </div >
    )
}

export default Donar;